.header{
    position: relative;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 7rem;
    .logo{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        width: 40%;
        height: 100%;
        img{
            margin: 1rem;
            cursor: pointer;
        }
    }
}

.logo{
    img{
        height: 4rem;
    }
}

.hamburguer{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 30%;
    margin-right: 4rem;

    i{
        color: $dorado--claro;
        margin: 1rem;
        cursor: pointer;
    }
    .active{
        color: white;
    }
}
.lista{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 4rem;
    position: absolute;
    background-color: $blanco;
    padding: 0;
    margin: 0;
    color: $blanco;
    top: 7rem;
    left: -100rem;
    transition: all .3s ease-in-out;
    width: 100%;
    height: calc(100vh - 7rem);
    z-index: 2;

    li{
        list-style: none;
        cursor: pointer;
        color: $dorado--claro;
        font-size: 2rem;
        font-weight: $regular;
        text-transform: uppercase;
        padding: 1rem;
        text-align: center;
    }
    li:hover{
        background-color: $dorado--oscuro;
        color: $blanco;
    }

    
}
.mostrar{
    left: 0;
    transition: all .3s ease-in-out;
}

@media ( min-width: 765px){

    .header{
        padding: 1rem;
        .logo{
            justify-content: flex-start;
            width: 20%;
        }
        
        .hamburguer{
            display: none;
        }

        .lista{
            background-color: $negro--intenso;
            flex-direction: row;
            justify-content: flex-end;
            left: 0;
            position: static;
            width: 70%;
            height: 100%;
            background-color: transparent;
            ul{
                margin: 0;
                padding: 2rem;
                display: flex;
                gap: 2rem;
                padding: 0;
                width: 100%;
                justify-content: flex-end;
                li{
                    font-size: 2rem;
                }
            }
        }

}

}