.proyectos{
    height: 100%;
    width: 100%;
    transform: translateY(90rem);
}

.cards--items{
    height: 100%;
    overflow: hidden;
    .card{
        height: 37rem !important;
        img{
            height: 80%;
        }
        p{
            height: 20%;
        }
    }
}
.glider-dots{
    button{
        background-color: $celeste;
        height: 1rem;
        width: 2rem;
    }
}
.glider-dot.active{
    background-color: $celeste--oscuro !important;
}
.glider-dot:hover{
    background-color: $celeste--oscuro !important;
}