.theme{
    width: 10%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    height: 100%;
    .checkbox{
        opacity: 0;
        position: absolute;
    }
    .label{
        position: absolute;
        border: black 1px solid;
        background-color: $blanco;
        border-radius: 2rem;
        display: flex;
        height: 3rem;
        width: 6rem;
        justify-content: space-between;
        align-items: center;
        padding: .5rem;
        margin: 0;
        .moon{
            color: #f1c40f;
        }
        .sun{
            color: #f39c12;
        }
    }
    .ball{
        position: absolute;
        background-color: $negro--intenso;
        height: 2.5rem;
        width: 2.5rem;
        border-radius: 50%;
        transition: transform 0.2s linear;
    }

}

.checkbox:checked + .label .ball {
	transform: translateX(2.5rem);
}
body{
    transition: background 0.2s linear;
}
body.dark{
    background-color: $negro--intenso;
}