.front--div{
    width: 100%;
    height: calc(100vh - 7rem);
    padding: .5rem;
    display: grid;
    grid-template-areas: 'img sociales'
                        'img sociales'
                        'text  text'
                        'text2  text2'
                        'skills skills';
    grid-template-rows: 40% 40% 6% 6% 8%;
    grid-template-columns: 90% 10%;
    .front--div--img{
        width: 100%;
        height: 100%;
        bottom: 0;
        margin-bottom: 3rem;
        grid-area: img;
        img{
            height: 100%;
            width: 100%;
            object-fit: contain;         
        }
    }
    .cuadro--der{
        grid-area: sociales;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 2rem;
        color: $dorado--claro;
    }
    .front--div--text{
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: .5rem;
        grid-area: text;
        h1{
            font-weight: $bold;
            text-transform: uppercase;
            color: $dorado--claro;
            font-size: 5rem;
            margin: 0;
        }
    }
    .front--div--text2{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 1rem;
        grid-area: text2;
        h2{
            color: $dorado--claro;
            font-size: 1.5rem; 
            margin: 0;
            font-weight: $regular;
        }      
    }
    .iconos{
        margin-top: 2rem;
        color: $dorado--claro;
        display: flex;
        gap: 3rem;
        grid-area: skills;
        justify-content: center;
        align-items: center;
    }
    .sobre--mi, .proyectos, .button{
        display: none;
    }
}

@media (min-width: 765px)
{
    .front--div{   
        grid-template-areas: 'img text text2 skills'
                            'img text about skills'
                            'img  text proyects skills'
                            'img text mensaje sociales';
        grid-template-rows: 10% 20% 50% 20%;
        grid-template-columns: 30% 20% 45% 5%;
        .front--div--img{
            height: 100%;
            display: flex;
            align-items: center;
            img{
                height: 80%;
                width: 100%;
            }
        }
        .front--div--text{
            grid-area: text;
            padding: 0 2rem;
            h1{
                transform: rotate(-90deg);
                font-size: 20rem;
                width: 100%;
                text-align: center;
                display: flex;
                justify-content: center;
                color: $mostaza;
            }
            h1::after{
                position:absolute;
                content: "apolo";
                transform: translateX(1rem);
                color: $dorado--claro;
                z-index: -1;
            }
        }
        .front--div--text2{
            grid-area: text2;
            height: 100%;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            flex-direction: column;
            gap: 1rem;
            h2{
                color: $dorado--claro;
                font-size: 2rem; 
                margin: 0;
                font-weight: $regular;
            }
        }
        .iconos{
            flex-direction: column;
            margin: 0;
            i{
                height: 5rem;
            }
        }
        
        .sobre--mi, .proyectos, .button{
            display: block;
        }
        
        .sobre--mi{
            padding: 2rem;
            p{
                font-size: 1.5rem;
            }
        }
        .proyectos{
            width: 100%;
            text-align: center;
        }
        .button{
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            width: 100%;
            text-align: center;
            color: $dorado--claro;
            p{
                color: $dorado--claro;
            }
        }
    }

}