.text-center {
  text-align: center;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Kanit";
  font-size: 1.6rem;
  background-color: #f8f9fd;
}

p {
  color: #5f571a;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  font-family: "Kanit";
  font-weight: 900;
  color: #5f571a;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

.header {
  position: relative;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 7rem;
}
.header .logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 40%;
  height: 100%;
}
.header .logo img {
  margin: 1rem;
  cursor: pointer;
}

.logo img {
  height: 4rem;
}

.hamburguer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 30%;
  margin-right: 4rem;
}
.hamburguer i {
  color: #ad9d4a;
  margin: 1rem;
  cursor: pointer;
}
.hamburguer .active {
  color: white;
}

.lista {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 4rem;
  position: absolute;
  background-color: #f8f9fd;
  padding: 0;
  margin: 0;
  color: #f8f9fd;
  top: 7rem;
  left: -100rem;
  transition: all 0.3s ease-in-out;
  width: 100%;
  height: calc(100vh - 7rem);
  z-index: 2;
}
.lista li {
  list-style: none;
  cursor: pointer;
  color: #ad9d4a;
  font-size: 2rem;
  font-weight: 400;
  text-transform: uppercase;
  padding: 1rem;
  text-align: center;
}
.lista li:hover {
  background-color: #5f571a;
  color: #f8f9fd;
}

.mostrar {
  left: 0;
  transition: all 0.3s ease-in-out;
}

@media (min-width: 765px) {
  .header {
    padding: 1rem;
  }
  .header .logo {
    justify-content: flex-start;
    width: 20%;
  }
  .header .hamburguer {
    display: none;
  }
  .header .lista {
    background-color: #020808;
    flex-direction: row;
    justify-content: flex-end;
    left: 0;
    position: static;
    width: 70%;
    height: 100%;
    background-color: transparent;
  }
  .header .lista ul {
    margin: 0;
    padding: 2rem;
    display: flex;
    gap: 2rem;
    padding: 0;
    width: 100%;
    justify-content: flex-end;
  }
  .header .lista ul li {
    font-size: 2rem;
  }
}
.front--div {
  width: 100%;
  height: calc(100vh - 7rem);
  padding: 0.5rem;
  display: grid;
  grid-template-areas: "img sociales" "img sociales" "text  text" "text2  text2" "skills skills";
  grid-template-rows: 40% 40% 6% 6% 8%;
  grid-template-columns: 90% 10%;
}
.front--div .front--div--img {
  width: 100%;
  height: 100%;
  bottom: 0;
  margin-bottom: 3rem;
  grid-area: img;
}
.front--div .front--div--img img {
  height: 100%;
  width: 100%;
  object-fit: contain;
}
.front--div .cuadro--der {
  grid-area: sociales;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  color: #ad9d4a;
}
.front--div .front--div--text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.5rem;
  grid-area: text;
}
.front--div .front--div--text h1 {
  font-weight: 700;
  text-transform: uppercase;
  color: #ad9d4a;
  font-size: 5rem;
  margin: 0;
}
.front--div .front--div--text2 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
  grid-area: text2;
}
.front--div .front--div--text2 h2 {
  color: #ad9d4a;
  font-size: 1.5rem;
  margin: 0;
  font-weight: 400;
}
.front--div .iconos {
  margin-top: 2rem;
  color: #ad9d4a;
  display: flex;
  gap: 3rem;
  grid-area: skills;
  justify-content: center;
  align-items: center;
}
.front--div .sobre--mi, .front--div .proyectos, .front--div .button {
  display: none;
}

@media (min-width: 765px) {
  .front--div {
    grid-template-areas: "img text text2 skills" "img text about skills" "img  text proyects skills" "img text mensaje sociales";
    grid-template-rows: 10% 20% 50% 20%;
    grid-template-columns: 30% 20% 45% 5%;
  }
  .front--div .front--div--img {
    height: 100%;
    display: flex;
    align-items: center;
  }
  .front--div .front--div--img img {
    height: 80%;
    width: 100%;
  }
  .front--div .front--div--text {
    grid-area: text;
    padding: 0 2rem;
  }
  .front--div .front--div--text h1 {
    transform: rotate(-90deg);
    font-size: 20rem;
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
    color: #e0c93c;
  }
  .front--div .front--div--text h1::after {
    position: absolute;
    content: "apolo";
    transform: translateX(1rem);
    color: #ad9d4a;
    z-index: -1;
  }
  .front--div .front--div--text2 {
    grid-area: text2;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
  }
  .front--div .front--div--text2 h2 {
    color: #ad9d4a;
    font-size: 2rem;
    margin: 0;
    font-weight: 400;
  }
  .front--div .iconos {
    flex-direction: column;
    margin: 0;
  }
  .front--div .iconos i {
    height: 5rem;
  }
  .front--div .sobre--mi, .front--div .proyectos, .front--div .button {
    display: block;
  }
  .front--div .sobre--mi {
    padding: 2rem;
  }
  .front--div .sobre--mi p {
    font-size: 1.5rem;
  }
  .front--div .proyectos {
    width: 100%;
    text-align: center;
  }
  .front--div .button {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    text-align: center;
    color: #ad9d4a;
  }
  .front--div .button p {
    color: #ad9d4a;
  }
}
.proyectos {
  height: 100%;
  width: 100%;
  transform: translateY(90rem);
}

.cards--items {
  height: 100%;
  overflow: hidden;
}
.cards--items .card {
  height: 37rem !important;
}
.cards--items .card img {
  height: 80%;
}
.cards--items .card p {
  height: 20%;
}

.glider-dots button {
  background-color: #92c0cf;
  height: 1rem;
  width: 2rem;
}

.glider-dot.active {
  background-color: #558691 !important;
}

.glider-dot:hover {
  background-color: #558691 !important;
}

.theme {
  width: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  height: 100%;
}
.theme .checkbox {
  opacity: 0;
  position: absolute;
}
.theme .label {
  position: absolute;
  border: black 1px solid;
  background-color: #f8f9fd;
  border-radius: 2rem;
  display: flex;
  height: 3rem;
  width: 6rem;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 0;
}
.theme .label .moon {
  color: #f1c40f;
}
.theme .label .sun {
  color: #f39c12;
}
.theme .ball {
  position: absolute;
  background-color: #020808;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 50%;
  transition: transform 0.2s linear;
}

.checkbox:checked + .label .ball {
  transform: translateX(2.5rem);
}

body {
  transition: background 0.2s linear;
}

body.dark {
  background-color: #020808;
}

body {
  overflow-x: hidden;
}

.wrapper .overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  z-index: 2;
}
.wrapper .first {
  background-color: #ad9d4a;
}
.wrapper .second {
  background-color: #ad9d4a;
  left: 33.3%;
}
.wrapper .third {
  background-color: #ad9d4a;
  left: 66.6%;
}